class Sequence extends React.Component {
  constructor(props) {
    super(props);
    this.state = { step: 1 };
  }

  componentDidMount() {
    this.timerID = setInterval(() => this.tick(), 1000);
  }

  render() {
    return (
      <div>
        <p>{this.state.step}</p>
        {this.props.children}
      </div>
    );
  }
}

export default Sequence;
